export const mutations = {
    initAll(state, { banner, classify, hots }) {
        state.banner = banner;

        for (let key in state.pic) {
            for (let i = 0; i < classify.length; i++) {
                if (classify[i].typeDesc == key) {
                    classify[i].pic = state.pic[key]
                }
            }
        }

        state.classify = classify;
        state.hots = hots;
    }
}