export const getters = {
    isCheckedAll(state) {

        if (state.allLove.length == 0) {
            return false;
        }
        // console.log("我改变了")
        // 根据数据改变判断是否需要全选
        for (let i = 0; i < state.allLove.length; i++) {
            if (!state.allLove[i].ischecked) {
                return false;
            }
        }
        return true;
    }
}