export const mutations = {
    changeData(state, { key, value }) {
        state[key] = value;
    },
    addInTag(state, { data, item }) {

        data.tag.push(item);

        state.addressInfo = data;

    },
    closeTag(state, index) {
        state.addressInfo.tag.splice(index, 1);
    },
    // 数据置空
    emptyData(state) {
        state.addressInfo = {};
        state.title = '';
    }
}