import Vue from 'vue'
import Vuex from 'vuex'
import { mainModule } from './main/store'
import { loginModule } from './login/store'
import { homeModule } from './home/store'
import { menuModule } from './menu/store'
import { detailModule } from './detail/store'
import { shopcarModule } from './shopCar/store'
import { myModule } from './my/store'
import { loveModule } from './love/store'
import { addressModule } from './address/store'
import { editaddressModule } from './editaddress/store'
import { settlementModule } from './settlement/store'
import { postModule } from './post/store'
import { personalModule } from './personal/store'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 该用户是否登录
    isLogin: false

  },
  mutations: {
    // 改变登录状态
    changeLogin(state, { bool }) {
      state.isLogin = bool;
    }
  },
  modules: {
    mainModule,
    loginModule,
    homeModule,
    menuModule,
    detailModule,
    shopcarModule,
    myModule,
    loveModule,
    addressModule,
    editaddressModule,
    settlementModule,
    postModule,
    personalModule
  }
})
