
import ComponentRoute from './componentRoute'
export const routes = [
    {
        path: '/main',
        name: 'Main',
        component: ComponentRoute.MainComponent,
        children: [
            {
                path: 'home',
                name: 'Home',
                component: ComponentRoute.HomeComponent
            },
            {
                path: 'my',
                name: 'My',
                component: ComponentRoute.MyComponent
            },
            {
                path: 'menu',
                name: 'Menu',
                component: ComponentRoute.MenuComponent
            },
            {
                path: 'shopcar',
                name: 'ShopCar',
                component: ComponentRoute.ShopCarComponent
            },
        ]
    },
    {
        path: '/',
        name: 'Login',
        component: ComponentRoute.LoginComponent
    },
    {
        path: '/detail',
        name: 'Detail',
        component: ComponentRoute.DetailComponent

    },
    {
        path: '/love',
        name: 'Love',
        component: ComponentRoute.LoveComponent

    },
    {
        path: '/post',
        name: 'Post',
        component: ComponentRoute.PostComponent
    },
    {
        path: '/address',
        name: 'Address',
        component: ComponentRoute.AddressComponent
    },
    {
        path: '/settlement',
        name: 'Settlement',
        component: ComponentRoute.SettlementComponent,
        beforeEnter: (to, from, next) => {
            // 当它是订单页面返回的时候，直接跳转的首页
            if (from.name == 'Post') {
                next({ name: 'Home' });
            } else {
                next();
            }
        }
    },
    {
        path: '/editaddress/:id',
        name: 'Editaddress',
        props: (route) => ({ aid: route.params.aid }),
        component: ComponentRoute.EditaddressComponent
    },
    {
        path: '/personal',
        name: 'Personal',
        component: ComponentRoute.PersonalComponent

    },
    {
        path: '/forgetpwd',
        name: 'Forgetpwd',
        component: ComponentRoute.ForgetpwdComponent

    },
    {
        path: '/reset',
        name: 'Reset',
        component: ComponentRoute.ResetComponent
    },
    {
        path: '/retrieve',
        name: 'Retrieve',
        component: ComponentRoute.RetrieveComponent

    },
    {
        path: '*',
        redirect: { name: 'Home' },
        component: ComponentRoute.HomeComponent
    },


]