import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueCookies from 'vue-cookies'
// rem
import 'lib-flexible';
// 请求
import axios from 'axios'
import VueAxios from 'vue-axios'
// vant
import { Uploader, NumberKeyboard, PasswordInput, Dialog, Icon, Tag, Popup, CouponList, CouponCell, CellGroup, Cell, Button, SwipeCell, List, Tabbar, TabbarItem, NavBar, Field, Switch, Form, Toast, Lazyload, SwipeItem, Swipe, Stepper, GoodsAction, GoodsActionIcon, GoodsActionButton, CheckboxGroup, Checkbox, SubmitBar, Image, AddressEdit, AddressList, Area } from 'vant';


import filter from './filter/filter'


Vue.use(Uploader)
  .use(Icon)
  .use(Tag)
  .use(Popup)
  .use(CouponCell)
  .use(CouponList)
  .use(CellGroup)
  .use(Cell)
  .use(VueAxios, axios)
  .use(SwipeCell)
  .use(Button)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Field)
  .use(NavBar)
  .use(Switch)
  .use(Form)
  .use(Toast)
  .use(VueCookies)
  .use(Swipe)
  .use(Lazyload, {
    error: require('./assets/img/error.jpg'),
    loading: require('./assets/img/loading.gif')
  })
  .use(SwipeItem)
  .use(Stepper)
  .use(GoodsAction)
  .use(GoodsActionIcon)
  .use(GoodsActionButton)
  .use(CheckboxGroup)
  .use(Checkbox)
  .use(SubmitBar)
  .use(Image)
  .use(List)
  .use(AddressEdit)
  .use(AddressList)
  .use(Area)
  .use(Dialog)
  .use(PasswordInput)
  .use(NumberKeyboard);



Vue.config.productionTip = false

// 配置请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';

// 配置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002'


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
