export const mutations = {

    taggleState(state, { key }) {
        // 转换状态
        state[key] = !state[key]
    },

    changeData(state, { key, data }) {
        state[key] = data;
    },
    changeCheckedAll(state, { bool }) {
        state.allLove.forEach(v => {
            v.ischecked = bool
        })
    },
    delOne(state, { item }) {
        state.allLove = state.allLove.filter(v => v.id != item.id);
    },
    loadingOne(state, { t }) {
        let data = state.allLoveData.splice(0, state.count);
        setTimeout(() => {
            state.allLove.push(...data);
            t.loading = false;
            if (data.length < state.count) {
                state.finished = true;
            }

        }, 200)
    },
    empty(state) {
        state.allLove = [];
        state.isEdit = false;
        state.finished = false;
    }


}