export const mutations = {
    toggleData(state, { key }) {
        state[key] = !state[key];
    },
    changeData(state, { key, data }) {
        state[key] = data
    },
    deleteShopcart(state) {
        state.shopData = state.shopData.filter(v => !v.checked);
    },

    getFiveShop(state, { t }) {
        setTimeout(() => {  //模拟加载
            // 如果已经全选，那么去掉全选
            if (t.checkedAll) {
                t.checkedAll = false;
            }
            let template = state.shopDataAll.splice(0, state.count);
            state.shopData.push(...template);
            // 关闭加载

            t.loading = false;
            if (state.shopDataAll.length == 0) {
                state.finished = true;
            }

        }, 200)
    },

    empty(state) {
        state.shopDataAll = [];
        state.shopData = [];
        state.finished = false;
        state.totalPrice = 0;
        state.isEdit = false;
    }



}