export const getters = {
    // 总数
    total(state) {
        let sum = 0;
        state.goodsData.forEach(v => {
            sum += v.count;
        })

        return sum;
    },

    totalPrice(state) {
        let price = 0;
        state.goodsData.forEach(v => {
            price += v.price * 100 * v.count;
        })

        return (price / 100).toFixed(2);

    }
}