export const mutations = {
    changeData(state, { key, value }) {
        state[key] = value
    },
    // 数据置空
    emptyData(state) {
        state.selectAddress = '选择收货地址';
        state.goodsData = [];
        state.orderInfo = {
            // 手机
            phone: '',
            // 地址
            address: '',
            // 收信人
            receiver: ''
        }
    }
}