export const mutations = {
    getProductData(state, { data }) {
        state.productDetail = data;

        //根据数据把需要渲染的数据构造好

        /**
         *  [
         *     { 
         *      title:'规格',
         *      relus:[{text:'多糖' , isAct:'false'},{text:'多糖' , isAct:'true'}]
         *      }
         * ]
         */

        let arr = ['tem', 'milk', 'sugar', 'cream'];
        for (let i = 0; i < arr.length; i++) {
            // 拦截层，如果data数据为空，不加这个字段
            if (data[arr[i]] == "") {
                continue;
            }

            let o = {};
            o.title = data[arr[i] + '_desc'];
            o.relus = [];
            let relus = data[arr[i]].split('/');

            for (let j = 0; j < relus.length; j++) {

                o.relus.push({ text: relus[j], isAct: j == 0 })

            }

            state.showData.push(o);
        }
        state.totalPrice = data.price;

        state.contentDetail = data.desc.split(/\n/);


    },
    changeShow(state, { index, left }) {
        state.show = index;
        state.left = left;
    },
    clearDetail(state) {
        state.productDetail = {};
        state.showData = [];
        state.contentDetail = [];
        state.show = 0;
        state.left = 0;
        state.isLove = false;
    },
    addOne(state) {
        state.productDetail.count++;
        let count = state.productDetail.count;
        this.commit("detailModule/getTotalPrice", { count })
    },
    delOne(state) {
        if (state.productDetail.count == 1) {
            return
        }
        state.productDetail.count--;
        let count = state.productDetail.count;
        this.commit("detailModule/getTotalPrice", { count })
    },
    getTotalPrice(state, { count }) {
        state.totalPrice = (count * (state.productDetail.price * 100) / 100).toFixed(2);
    },
    loveToggle(state) {
        state.isLove = !state.isLove;
    },
    isLoveInit(state, { list }) { //根据初始请求来的收藏商品列表判断你是否已经收藏该商品

        if (list.length == 0) { //该商品没有被收藏
            state.isLove = false;
        } else {
            state.isLove = true;
        }


    },
    changeBadge(state, { data }) {

        state.badge = data || 0;
    }
}