import Vue from 'vue'
import { format } from 'date-fns'
Vue.filter('makeNoon', function (date) {

    let hour = date.getHours();
    if (hour < 6) {
        return '凌晨'
    } else if (hour < 10) {
        return '早上'
    } else if (hour < 14) {
        return '中午'
    } else if (hour < 18) {
        return '下午'
    } else if (hour < 21) {
        return '晚上'
    } else {
        return '深夜'
    }
})

Vue.filter('formatDate', function (date) {
    return format(date, 'yyyy-MM-dd HH:mm:ss');
})

Vue.filter('formatOid', function (oid) {
    return 'No' + oid.slice(2);
})