export const getters = {
    asideData(state, getters, rootState, rootGetters) {
        // console.log("rootState==>", rootState.homeModule.classify)
        let classify = rootState.homeModule.classify;
        // 需要加上一个今日推荐
        classify.unshift({
            id: 0,
            type: '1',
            typeDesc: '今日推荐'
        })
        return classify
    }
}